/* @jsx jsx */
import {jsx} from "@emotion/react"

import {Helmet} from "react-helmet"
import {Link, graphql} from "gatsby"

import PageLayout from "../components/PageLayout";

interface RecentPageProps {
    data: {
        allMarkdownRemark: {
            edges: {
                node: {
                    frontmatter: {
                        pageTitle: string;
                        slug: string;
                        last_modified: string;
                    }
                }
            }[];
        }
    }
}

const RecentPage = (props: RecentPageProps) => {
    const {data} = props;

    const content = <div>
        <h1>Recently Updated</h1>
        {data.allMarkdownRemark.edges.map(tag => (
            <div key={tag.node.frontmatter.slug}>
                <small>
                    {tag.node.frontmatter.last_modified}
                </small>
                <span> - </span>
                <Link to={tag.node.frontmatter.slug}>
                    {tag.node.frontmatter.pageTitle}
                </Link>
            </div>
        ))}
    </div>

    return (
        <div>
            <Helmet>
                <meta charSet="utf-8"/>
                <meta name="viewport" content="width=device-width, initial-scale=1"/>
                <title>Arshi Annafi | Recently Updated</title>
                <meta name="description"
                      content="List of pages that are updated recently"/>
            </Helmet>
            <div>
                <PageLayout content={content}/>
            </div>
        </div>
    )
}

export default RecentPage

export const pageQuery = graphql`
  query {
    allMarkdownRemark(
      sort: {order: DESC, fields: frontmatter___initially_published}
    ) {
      edges {
        node {
          frontmatter {
            slug
            pageTitle
            last_modified
          }
        }
      }
    }
  }
`
